	module.exports = {"data":{"markdownRemark":{"html":"<h2>備考</h2>\n<p>2003年上旬に弟へ寄贈したPCだったが、弟がPC新調したそうで帰ってきた。実験用鯖PCとして活躍。</p>\n<p>そろそろデスクメインな環境に戻ろうかなと。またPC自作熱が再発し始めてきた。</p>\n<p>Windows7入れた時点でさすがにもう限界だろうということでネットオークションで1円にて売却。しかし後で低スペック用のテスト環境として最適だったのに、と激しく後悔した。グラボだけGeForce3～4Tiクラスのやつに変えて(プログラマブルシェーダは一応ほしい)、Windows7は駄目でもXP入れておけば最高の環境だったのに……orz</p>\n<h2>性能</h2>\n<ul>\n<li>CPU:intel Celeron(Coppermine-128K/600MHz)</li>\n<li>RAM:512MB(PC-133)</li>\n<li>VGA:nVidia GeForce2MX400(VRAM32MB)</li>\n<li>HDD:60GB+40GB(JBOD)</li>\n<li>CDD:CD-RW。詳細不明(x52、ジャンク品)</li>\n<li>FDD:3.5inch(3mode)×1</li>\n<li>NET:PLANEX GN-1200TW(1000Base-T)</li>\n<li>SOUND:onBoard</li>\n<li>OS:Fedora8→Windows7 Enterprise</li>\n<li>\n<p>1999年クラスの最新スペック</p>\n<h2>Windows Experience Index</h2>\n</li>\n</ul>\n<p>計測→スコアがありませんという謎現象orz</p>","frontmatter":{"cover":null,"date":"2007-04-14T17:00:00+09:00","strDate":"2007/4/14","title":"(2007-2010) 世界最速1号Ver2.1"}}},"pathContext":{"path":"/ja/blog/2007/04/14-2007-2010-世界最速1号ver2-1/","langKey":"ja"}}

